
@mixin link($link, $hover, $active, $visited) {
    a{
      color: $link;
  
      &:hover {
          color:$hover;
      } 
      &:active {
          color:$active;
      } 
      &:visited {
          color:$visited;
      } 
  }
}

@mixin padd($color, $padd...) {
    padding:$padd;
}

@mixin font ($color, $fontW) {
    font-size: $mainFontSize;
    font-weight: bold;
    font-family: Arial, Helvetica, sans-serif;
    color: $color;
    
}
